*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  body {
    font-family: 'Montserrat', sans-serif;
  }
  
  .app {
    background-image: url('./images/zetong-li-UC1HP-zhEmU-unsplash.jpg');
    /* background-size: cover; */
    background-size: 100% 100%;
    background-position: bottom;
    transition: 0.4 ease; 
  }
  
  .app.warm {
    background-image: url('./images/matteo-catanese-W96ghI48B5Q-unsplash.jpg');
  } 
  
  .app.hot {
    background-image: url('./images/sean-oulashin-KMn4VEeEPR8-unsplash.jpg');
  } 
  
  .app.cold {
    background-image: url('./images/stephen-meyers-rLBewYnoi6I-unsplash.jpg');
  } 
  
  main {
    min-height: 100vh; 
    background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0.1), rgba(0, 0, 0, 0.5));
    padding: 25px;
  }
  
  .search-box {
    width: 100%;
    margin: 0 0 75px;
    position: relative;
  }
  
  .autocomplete {
    position: absolute;
    left: 0;
    top: 50px;
    width: 100%;
    background: white;
    list-style: none;
    margin: 0;
    padding: 0;
    z-index: 111;
    max-height: 240px;
    height: auto;
    overflow: auto;
  }
  
  .autocomplete_item {
    padding: 10px;
  }
  
  .autocomplete_item:hover {
    background: rgba(180, 180, 180);
    cursor: pointer;
  }
  
  .search-box .search-bar {
    display: block;
    width: 100%;
    padding: 15px;
  
    appearance: none;
    background: none;
    border: none;
    outline: none;
  
    background-color: rgba(255, 255, 255, 0.5);
    border-radius: 0px 0px 16px 16px;
    margin-top: -25px;
  
    box-shadow: 0px 5px rgba(0, 0, 0, 0.2);
    
    color: #313131;
    font-size: 20px;
  
    transition: 0.4s ease;
  }
  
  .search-box .search-bar:focus {
    background-color: rgba(255, 255, 255, 0.85);
  }
  
  .location-box .location {
    color: #FFF;
    font-size: 42px;
    font-weight: 500;
    text-align: center;
    text-shadow: 3px 3px rgba(50, 50, 70, 0.5);
  }
  
  .location-box .date {
    color: #FFF;
    font-size: 20px;
    font-weight: 300;
    font-style: italic;
    text-align: center;
    text-shadow: 2px 2px rgba(50, 50, 70, 0.5);
  }
  
  .weather-box {
    text-align: center;
  }
  
  .weather-box .temp {
    position: relative;
    display: inline-block;
    margin: 30px auto;
    background-color: rgba(255, 255, 255, 0.2);
    border-radius: 16px;
  
    padding: 15px 25px;
  
    color: #FFF;
    font-size: 102px;
    font-weight: 900;
  
    text-shadow: 3px 6px rgba(50, 50, 70, 0.5);
    text-align: center;
    box-shadow: 3px 6px rgba(0, 0, 0, 0.2);
  }
  
  .weather-box, .weather, .humidity {
    color: #FFF;
    font-size: 48px;
    font-weight: 700;
    text-shadow: 3px 3px rgba(50, 50, 70, 0.5);
  }
  
  
  img {
    height: 120px;
    width: 120px;
  }
  
  
